<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full">
                        <h4 class="modal-title">{{ $t('Add Client') }}</h4>
                        <button type="button" class="close" v-on:click="Close()">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <Form ref="ValidateClient">
                            <dynamic-form lang="en" ref="AddClient" :buttons="buttons" :schema="FormSchema"
                                v-on:OnSubmit="onSubmit"></dynamic-form>
                        </Form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import services from "@/services/DataService";
export default {
    props: {
        callbackfunction: {
            type: Function
        }
    },
    data() {
        return {
            isLoading: false,
            TimeZoneList: [],
            ParentAccountList: [],
            CientOwnerList: [],
            fileData: [],
            UserType: 0,
            RoleList: [],
            FormSchema: [
                {
                    layoutType: "four",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('First Name'),
                            name: "First Name",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Last Name'),
                            name: "Last Name",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Email '),
                            name: "Email",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Gender'),
                            name: "Gender",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [{ value: "M", name: "Male" }, { value: "F", name: "Female" }],
                                showAddIcon: false,

                            },
                            validationRules: "",
                        },
                        {
                            astype: "RadioField",
                            label: this.$t('Account'),
                            name: "Account",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [{
                                    name: 'Existing', value: 1
                                }, {
                                    name: 'Create New', value: 2
                                }],
                                showAddIcon: false,
                                onChange: this.onChangeRadio
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Parent Account'),
                            name: "Parent Account",
                            value: "",
                            placeholder: "",
                            visibility: false,
                            config: {
                                options: [],
                                showAddIcon: false,

                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Account Name'),
                            name: "Account Name",
                            value: "",
                            placeholder: "",
                            visibility: false,
                            validationRules: { "required": false, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Client Owner'),
                            name: "Client Owner",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,

                            },
                            validationRules: "",
                        },

                    ]
                },
                {
                    group_name: "Advanced Setting",
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('TimeZone'),
                            name: "TimeZone",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,

                            },
                            validationRules: "",
                        },
                        {
                            astype: "SelectField",
                            label: 'Date Format',
                            name: "Date",
                            value: "",
                            config: {
                                options: [{ name: "M/D/YYYY", value: "M/D/YYYY" }, { name: "DD/MM/YYYY", value: "DD/MM/YYYY" }]
                                // options: [{ name: "M/d/yyyy", value: "M/D/YYYY" }, { name: "dd/MM/yyyy", value: "DD/MM/YYYY" }]
                            },
                            placeholder: "Select",
                            validationRules: { "required": false, },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Role'),
                            name: "Role",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,

                            },
                            validationRules: "",
                        },
                        {
                            astype: "FileField",
                            showLabel: false,
                            name: "Attachment",
                            placeholder: "",
                            validationRules: "",
                            FieldIndex: 0,
                            config: {
                                maxSize: 5,
                                accept: [
                                    {
                                        fileType: 'png',
                                        iconClass: 'fa-file-image-o'
                                    },
                                    {
                                        fileType: 'jpg',
                                        iconClass: 'fa-file-image-o'
                                    }
                                ],
                                onChange: this.GetBase64String,
                                showAddIcon: false,
                                onAddButtonClick: '',
                                multiple: false
                            },
                        }
                    ]
                },
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    onClick: this.SaveData
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
        }
    },
    created: async function () {
        await this.GetTimeZoneList();
        await this.GetParentAccountList();
        await this.GetClientOwnerList();
        await this.GetRoleList();
        vm.UserType = vm.GetUserInfo.UserType;
    },
    methods: {
        Close: function () {
            var vm = this;
            vm.$parent.isManageClient = false;
        },
        GetTimeZoneList: async function () {
            var vm = this;
            await services.GetTimeZoneList().then(async (response) => {
                if (response.data != null && response.data != "") {
                    if (response.data.length > 0) {
                        vm.TimeZoneList = response.data;
                        vm.TimeZoneList.forEach((item, index) => {
                            (vm.FormSchema)[1].Data[0].config.options.push({
                                value: item.code,
                                name: item.value
                            })
                        });
                        vm.$refs.AddClient.UpdateKeyValue();
                    }
                }
            });
        },
        onChangeRadio: async function (e, x, y) {
            var vm = this;
            //debugger;
            if (x.value == 1) {
                vm.$refs.AddClient.updateProperty("Parent Account", "visibility", true);
                vm.$refs.AddClient.updateProperty("Account Name", "visibility", false);
            }
            else {
                vm.$refs.AddClient.updateProperty("Account Name", "visibility", true);
                vm.$refs.AddClient.updateProperty("Parent Account", "visibility", false);
                vm.$refs.AddClient.UpdateKeyValue();
            }

        },
        GetParentAccountList: async function () {
            var vm = this;
            var params = `moduleName=Ticketing&type=TALYGEN_ACCOUNT`
            await services.GetDDLData(params).then(async (response) => {
                if (response.data != null && response.data != "") {
                    if (response.data.DATA.length > 0) {
                        vm.ParentAccountList = response.data.DATA;
                        vm.ParentAccountList.forEach((item, index) => {
                            (vm.FormSchema)[0].Data[5].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        });
                        vm.$refs.AddClient.UpdateKeyValue();
                    }
                }

            });
        },
        GetBase64String: function (evt) {

            try {

                var vm = this;
                //alert(vm.apiurl);
                vm.fileData = [];

                var f = evt.target.files[0]; // FileList object
                var reader = new FileReader();
                // Closure to capture the file information.
                reader.onload = (function (theFile) {

                    return function (e) {
                        var binaryData = e.target.result;
                        //Converting Binary Data to base 64
                        var base64StringEncoded = window.btoa(binaryData);
                        //showing file converted to base64

                        var fileName = f.name;
                        vm.fileName = fileName;
                        var n = fileName.lastIndexOf(".");
                        var extensionName = fileName.substring(n + 1, fileName.length);

                        vm.fileExt += "." + extensionName;
                        var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                        var fileSize = f.size;
                        vm.fileSize = fileSize;

                        // var ggg = vm.configData;
                        var fileObj = {
                            attachement_mime: attachemnetMime,
                            fileName: fileName,
                            base64String: base64StringEncoded,
                            fileSize: f.size,
                            contentType: f.type
                        };

                        vm.fileData.push(fileObj);
                    };
                })(f);
                // Read in the image file as a data URL.
                reader.readAsBinaryString(f);
            }
            catch (ex) {
            }
        },
        GetClientOwnerList: async function () {
            var vm = this;
            var params = `moduleName=Client&type=`
            await services.GetClientOwner(params).then(async (response) => {
                if (response.data != null && response.data != "") {
                    if (response.data.length > 0) {
                        vm.ParentAccountList = response.data;
                        vm.ParentAccountList.forEach((item, index) => {
                            (vm.FormSchema)[0].Data[7].config.options.push({
                                name: item.USERNAME,
                                value: item.USERID
                            })
                        });
                        vm.$refs.AddClient.UpdateKeyValue();
                    }
                }

            });
        },
        GetRoleList: async function () {
            //debugger
            var vm = this;
            var vm = this;
            var userTypeId = 0;
            if (vm.UserType == "CA") {
                userTypeId = 1;
            }
            else if (vm.UserType == "PM") {
                userTypeId = 2;
            }
            else if (vm.UserType == "NU") {
                userTypeId = 3;
            }
            else if (vm.UserType == "CL") {
                userTypeId = 4;
            }
            var params = `moduleName=Ticketing&type=TALYGEN_ROLE&refId=${userTypeId}`
            await services.GetDDLData(params).then(async (response) => {
                if (response.data != null && response.data != "") {
                    if (response.data.DATA.length > 0) {
                        vm.RoleListList = response.data.DATA;
                        vm.RoleList.forEach((item, index) => {
                            (vm.FormSchema)[1].Data[2].config.options.push({
                                name: item.value,
                                value: item.name
                            })
                        });
                        vm.$refs.AddClient.UpdateKeyValue();
                    }
                }

            });
        },
        SaveData: function () {
            var vm = this;
            vm.$refs.ValidateClient.validate().then(result => {
                if (result) {
                    let addClient = {
                        "FirstName": vm.FormSchema[0].Data[0].value,
                        "LastName": vm.FormSchema[0].Data[1].value,
                        "Email": vm.FormSchema[0].Data[2].value,
                        "Gender": vm.FormSchema[0].Data[3].value,
                        "Account": vm.FormSchema[0].Data[4].value,
                        "ParentAccounts": vm.FormSchema[0].Data[5].value,
                        "AccountName": vm.FormSchema[0].Data[6].value,
                        "ClientOwner": vm.FormSchema[0].Data[7].value,
                        "TimeZone": vm.FormSchema[1].Data[0].value,
                        "DateFormat": vm.FormSchema[1].Data[1].value,
                        "Role": vm.FormSchema[1].Data[2].value == "" ? null : vm.FormSchema[1].Data[2].value,
                        "DepartmentId": null,
                        "DesignationId": null,
                        "CompanyName": null,
                        "SuperVisorId": null,
                        "UserTypeId": 4,
                        "HomeBuisnessUnitId": null,
                        "PayClass": null,
                        "JobType": null,
                        "StartDate": null,
                        "Minitial": null,
                        "FileData": vm.fileData
                    }
                    //debugger;
                    services.AddClient(addClient).then(function (response) {
                        //debugger;
                        if (response.data[0].Status == 'Success') {
                            vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Client")]), "success", true, vm.$t("Alert"));
                            if (vm.$parent.FormSchema.length > 0) {
                                vm.$parent.FormSchema.forEach((group, gi) => {
                                    group.Data.forEach((field, fi) => {
                                        if (field.name == "on_behalf_consumer_id") {
                                            vm.callbackfunction('CLIENTS', field, null);
                                            vm.Close();
                                        }
                                    });
                                });
                            }
                        }
                        else {
                            vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                        }
                    })
                }
            })
        }
    }
}
</script>