<template>
    <section class="main-content">
        <!-- <loader :is-visible="isLoading"></loader> -->
        <div class="row">
            <div class="col-md-12 padding-t_8">
                <div class="theme-primary partition-full">
                    <span class="p-name text-white"> {{ $t('AddTicket') }}</span>
                    <span class="float-right">
                        <RouterLink to="/Ticketing/Index" class="p-action-btn text-white float-right">
                            <em class="fa fa-angle-double-left"></em>
                            <br />
                            {{ $t('BacktoList') }}
                        </RouterLink>
                    </span>
                </div>
            </div>
        </div>
        <div class="border p-3" v-if="isSuccessMessage">
            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12  ticket-thankyou text-center">
                <span class="checksign text-success"><i class="fa fa-check"></i></span>
                <h1 class="mt-4 mb-4">{{ $t('ThankYou') }}</h1>

                <div class="pb-4 ticket-number">
                    <P>{{ saveTicketStatus }}</P>
                    <div
                        style="font-size: 20px;position: relative;background: #fff3cd;padding: 35px 30px; margin: 20px; border: 4px dotted #856404;display: inline-block;color: #856404;">
                        {{ TicketNumber }}</div>
                </div>
            </div>
        </div>
        <Form ref="AddTicket">
            <dynamic-form ref="AddTicketForm" :buttons="buttons" :schema="FormSchema" v-if="!isSuccessMessage">
                <template v-slot:tgslot-comment="{ data }">

                    <ckeditor :editor="editor" @ready="onReady" v-model="data.fieldInfo.value"
                        @input="updateCkeditorModel($event, data)" :class="{ 'is-invalid': data.error != '' }">
                    </ckeditor>
                    <span class="invalid-feedback">{{ data.error }}</span>
                    <input type="hidden" v-model="data.fieldInfo.value" />
                </template>
                <template v-slot:tgslot-addresFields="{ data }" v-if="isForServiceRequest">
                    <div class="row">
                        <div class="col-md-6">
                            <dynamic-form ref="AddTicketForm1" :schema="StaticObj"></dynamic-form>
                        </div>
                        <div class="col-md-6">
                            <ManageMap class="row" mapType="MapBox" mapContainer="AddTicket" :latLng="latLng"
                                :config="config" mapHeight="355px" v-if="isShowMap" @GetAddress="GetAddress">
                            </ManageMap>
                        </div>
                    </div>
                </template>
            </dynamic-form>
        </Form>
        <ManageCategory v-if="isManageCategory" modulename="Ticketing" submodulecode="Ticketing"
            v-bind:callbackfunction="GetDropDownData"></ManageCategory>
        <ManageClient v-if="isManageClient" v-bind:callbackfunction="GetDropDownData"></ManageClient>
    </section>
</template>
<script>
import DataServices from "@/services/DataService";
import ManageMap from './../../components/Common/MapComponent.vue';
import ManageCategory from './DetailsComponent/AddManageCategoryModal.vue';
import ManageClient from '../../components/Modal/modaladdClient.vue'
export default {
    components: {
        ManageMap,
        ManageCategory,
        ManageClient
    },
    data: function () {
        return {
            config: {
                isShowDrawButton: true,
                isShowSearchBox: true
            },
            FormSchema: [],
            CountryDDl: '',
            isSuccessMessage: false,
            fileMulitpleData: [],
            isShowMap: true,
            latLng: '',
            UserListDLL: '',
            CountryDDl: '',
            StateDDl: '',
            ticketType: '',
            Latitude: '',
            Longitude: '',
            showTourGuide: true,
            isForServiceRequest: false,
            StaticObj: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('ClientAddress'),
                            name: "AddrTo",
                            value: "",
                            disabled: false,
                            config: {
                                options: [],
                                onChange: this.onChangeDropdown

                            },
                            placeholder: this.$t('Select'),
                            validationRules: "",

                        },
                    ]
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Address'),
                            name: "Address",
                            value: "",
                            disabled: true,
                            placeholder: this.$t('PleaseEnterAddress'),
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Country'),
                            name: "country_id",
                            value: "",
                            disabled: true,
                            config: {
                                options: [],
                                onChange: this.GetStateDDL
                            },
                            placeholder: this.$t('Select'),
                            validationRules: "",
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('State'),
                            name: "State",
                            value: "",
                            disabled: true,
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select'),
                            validationRules: "",
                        },
                        {
                            astype: "TextField",
                            label: this.$t('County'),
                            name: "County",
                            value: "",
                            disabled: true,
                            placeholder: this.$t('PleaseEnterCounty'),
                            validationRules: "",
                        },
                        {
                            astype: "TextField",
                            label: this.$t('City'),
                            name: "City",
                            value: "",
                            disabled: true,
                            placeholder: this.$t('PleaseEnterCityName'),
                            validationRules: "",
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Street'),
                            name: "Street",
                            value: "",
                            disabled: true,
                            placeholder: this.$t('PleaseEnterStreet'),
                            validationRules: "",
                        },
                        {
                            astype: "TextField",
                            label: this.$t('ZipCode'),
                            name: "Zip",
                            value: "",
                            disabled: true,
                            placeholder: this.$t('PleaseEnterZipCode'),
                            validationRules: "",
                        },
                        {
                            astype: "TextField",
                            label: this.$t('NoteAboutAddress'),
                            name: "Notes",
                            value: "",
                            disabled: false,
                            placeholder: this.$t('PleaseEnterDescription'),
                            validationRules: "",
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    id: "ancsaveuserticket",
                    class: "btn btn-warning save ancsaveuserticket",
                    text: "<i class='fa fa-save pr-2'></i> Save As Draft",
                    value: "DRAFT",
                    onClick: this.saveForm
                },
                {
                    type: "submit",
                    id: "ancsaveuserticket",
                    class: "btn btn-success save ancsaveuserticket",
                    text: "<i class='fa fa-save pr-2'></i> Submit",
                    onClick: this.saveForm
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i> Cancel",
                    onClick: this.Close
                }
            ],
            isManageCategory: false,
            isManageClient: false,
            AllowedFileType: [],
            editor: this.CLASSIC_EDITOR,
        }
    },
    created: function () {
        var vm = this;
        vm.userid = this.GetUserInfo.ID
        if (!vm.isBlank(vm.$route.params.id) && vm.$route.params.id != "0") {
            let ticket_id = decodeURIComponent(vm.$route.params.id);
            let ticket_type = vm.$route.params.ticket_Type;
            vm.getForm(ticket_id);
        }
        else {
            if (vm.$route.params.id == "0") {
                vm.getForm(0);

            }
            else {
                vm.$router.push({ name: "add", params: { id: 0 } });
                vm.getForm(0);
            }
        }
        vm.GetCountryDDL();
        vm.GetDDLOnBehalf('USERS');
        vm.GetDDLOnBehalf('CLIENTS');
    },
    methods: {
        GetAddress: function (data) {
            debugger;
            var vueObj = this;
            vueObj.Latitude = '';
            vueObj.Longitude = '';
            //debugger;
            var indexValue = data.length - 1;
            let filteredCountry = vueObj.CountryDDl.filter((item) => {
                return item.name === data[indexValue].CompleteAddress.Country.LongName;
            });
            vueObj.StaticObj[1].Data[0].value = data[indexValue].CompleteAddress.FullAddress;
            var filterCountry = filteredCountry == '' ? '' : filteredCountry[0].value;
            vueObj.StaticObj[1].Data[1].value = filterCountry;
            vueObj.GetStateDDL(null, filterCountry);
            setTimeout(function () {
                let filteredState = vueObj.StateDDl.filter((item) => {
                    return item.name === data[indexValue].CompleteAddress.AdministrativeAreaLevel1.LongName;
                });
                var filterState = filteredState == '' ? '' : filteredState[0].value;
                vueObj.StaticObj[1].Data[2].value = filterState;
            }, 1000);
            vueObj.StaticObj[1].Data[3].value = data[indexValue].CompleteAddress.AdministrativeAreaLevel2.LongName;
            vueObj.StaticObj[1].Data[4].value = data[indexValue].CompleteAddress.City.LongName;
            vueObj.StaticObj[1].Data[5].value = data[indexValue].CompleteAddress.AdministrativeAreaLevel3.LongName;
            vueObj.StaticObj[1].Data[6].value = data[indexValue].CompleteAddress.PinCode;
            var dataLat1 = ''; var dataLat2 = ''; var dataLng1 = ''; var dataLng2 = '';
            data[indexValue].LatLng.forEach((item, index) => {
                if (index == 0) {
                    dataLat1 = item.Lat;
                    dataLat2 = item.Lng;
                }
                else {
                    dataLng1 = item.Lat;
                    dataLng2 = item.Lng;
                }
            });
            vueObj.Latitude = dataLng1 + ',' + dataLat2;
            vueObj.Longitude = dataLat1 + ',' + dataLng2;

            vueObj.FormSchema.forEach((group, gi) => {
                group.Data.forEach((field, fi) => {
                    if (group.group_name == "General Information" || group.group_name == "GeneralInformation") {
                        if (field.name == "location_se") {
                            field.value = vueObj.Latitude;
                        }
                        if (field.name == "location_nw") {
                            field.value = vueObj.Longitude;
                        }
                    }
                });
            });
        },
        onChangeDropdown: async function (e) {
            var vm = this;
            var dropName = e.target.name;
            var clientAddressId = '';
            if (dropName == "AddrTo") {
                clientAddressId = e.target.value;
            }
            vm.FormSchema.forEach((group, gi) => {
                group.Data.forEach((field, fi) => {
                    if (field.name == "on_behalf_consumer_id") {
                        let clientid = field.value.value;
                        if (!vm.isBlank(clientid) && clientAddressId != '') {
                            vm.BindAddressUsingClientAdrress(clientid, clientAddressId);
                        }
                    }
                });
            });
        },
        GetCountryDDL: async function (event) {
            ////debugger;
            var vm = this;
            vm.StaticObj[1].Data[1].config.options = [];
            vm.CountryDDl = '';
            var appendingUrl = `id=&&moduleName=TICKETING&type=${'COUNTRIES'}&search=&refId=`;
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                ////debugger;
                if (response.data) {
                    var json = response.data;
                    vm.CountryDDl = response.data.result.DATA;
                    if (json != null) {
                        response.data.result.DATA.forEach((item, index) => {
                            vm.StaticObj[1].Data[1].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },
        GetUserDDLOnBehalf: async function (type) {
            var vm = this;
            vm.UserListDLL = '';
            var url = `id=&moduleName=TICKETING&type=${type}&search=&refId=`;
            await DataServices.GetDDLData(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        vm.UserListDLL = response.data.DATA;
                    }
                }
            });
        },
        GetClientDDLOnBehalf: async function (type) {
            var vm = this;
            vm.ClientListDLL = '';
            var url = `id=&moduleName=TICKETING&type=${type}&search=&refId=`;
            await DataServices.GetDDLData(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        vm.ClientListDLL = response.data.DATA;
                    }
                }
            });
        },
        GetStateDDL: async function (event, id) {
            var vm = this;
            var CountryId = 0;
            if (event == null) {
                CountryId = id;
            } else {
                CountryId = event.target.value.toString();
            }
            vm.StaticObj[1].Data[2].config.options = [];
            vm.StateDDl = '';
            var appendingUrl = `id=&&moduleName=TICKETING&type=${'STATE'}&search=&refId=` + CountryId;
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    vm.StateDDl = response.data.result.DATA;
                    if (json != null) {
                        response.data.result.DATA.forEach((item, index) => {
                            vm.StaticObj[1].Data[2].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },
        BindAddressUsingClientAdrress: async function (clientid, clientAddressId) {
            ////debugger
            var vueObj = this;
            vueObj.Latitude = '';
            vueObj.Longitude = '';
            vueObj.latLng = '';
            var url = `id=${clientid}&moduleName=TICKETING&addressId=${clientAddressId}`;
            await DataServices.GetTicketClientAddress(url).then(response => {

                if (response.data.result != null && response.data.result.data.length > 0) {
                    vueObj.StaticObj[1].Data[0].value = response.data.result.data[0].USER_ADDRESS_1;
                    vueObj.StaticObj[1].Data[1].value = response.data.result.data[0].COUNTRY_ID;
                    vueObj.StaticObj[1].Data[2].value = response.data.result.data[0].CT[0].ST[0].STATE_ID;
                    vueObj.StaticObj[1].Data[4].value = response.data.result.data[0].USER_CITY;
                    vueObj.StaticObj[1].Data[6].value = response.data.result.data[0].USER_ZIPCODE;
                    vueObj.StaticObj[1].Data[7].value = response.data.result.data[0].USER_ADDRESS_DESC;
                    vueObj.Latitude = response.data.result.data[0].Latitude + ',' + response.data.result.data[0].Longitude;
                    vueObj.Longitude = response.data.result.data[0].Latitude + ',' + response.data.result.data[0].Longitude;
                    vueObj.FormSchema.forEach((group, gi) => {
                        group.Data.forEach((field, fi) => {
                            if (group.group_name == "General Information" || group.group_name == "GeneralInformation") {
                                if (field.name == "location_se") {
                                    field.value = vueObj.Latitude;
                                }
                                if (field.name == "location_nw") {
                                    field.value = vueObj.Longitude;
                                }
                            }
                        });
                    });
                    var obj = { location_nw: vueObj.Latitude }
                    vueObj.latLng = obj;
                }
            })
        },
        BindAddressInEdit: async function (data) {
            //debugger
            var vueObj = this;
            vueObj.Latitude = '';
            vueObj.Longitude = '';
            vueObj.latLng = '';
            if (data != null) {
                vueObj.StaticObj[1].Data[0].value = data[0].ADDRFROM;
                vueObj.StaticObj[1].Data[1].value = data[0].COUNTRY_ID;
                vueObj.StaticObj[1].Data[2].value = data[0].STATE;
                vueObj.StaticObj[1].Data[4].value = data[0].CITY;
                vueObj.StaticObj[1].Data[3].value = data[0].COUNTY;
                vueObj.StaticObj[1].Data[6].value = data[0].ZIP;
                vueObj.StaticObj[1].Data[5].value = data[0].DIGSTREET;
                vueObj.StaticObj[1].Data[7].value = data[0].NOTES;
                vueObj.Latitude = data[0].LOCATION_NW;
                vueObj.Longitude = data[0].LOCATION_SE;
                vueObj.FormSchema.forEach((group, gi) => {
                    group.Data.forEach((field, fi) => {
                        if (group.group_name == "General Information" || group.group_name == "GeneralInformation") {
                            if (field.name == "location_se") {
                                field.value = vueObj.Latitude;
                            }
                            if (field.name == "location_nw") {
                                field.value = vueObj.Longitude;
                            }
                        }
                    });
                });
                var obj = { location_nw: vueObj.Latitude }
                vueObj.latLng = obj;
            }

        },
        onReady(editor) {
            editor.ui.getEditableElement().parentElement.insertBefore(
                editor.ui.view.toolbar.element,
                editor.ui.getEditableElement()
            );
            let mediaIcon = document.getElementsByClassName('ck-dropdown')[2]
            mediaIcon.remove();
            var divToRemove = document.querySelector('.ck-file-dialog-button');
            divToRemove.remove();
            let ArrowIcon = document.getElementsByClassName('ck ck-button ck-off')[8]
            ArrowIcon.remove();
        },
        onChangeEventForFields: function (x, y, z) {
            let ths = this;
            if (x == "FILL_DEPENDENT") {
                y.config.options = [];
                var url = `id=&moduleName=TICKETING&type=${y.config.fieldCode}&search=&refId=${z}`;
                DataServices.GetDDLData(url).then(function (response) {
                    if (response.data) {
                        var json = response.data;
                        if (json != null) {
                            y.config.options = response.data.DATA;
                            ths.$refs.AddTicketForm.UpdateKeyValue();
                        }
                    }
                });
            } else {
                if (y.name == "attachment") {
                    ths.GetBase64String(y.value);
                } else if (y.name == "ticket_category_id") {
                    let catOptions = ths.$refs.AddTicketForm.getPropertyValue("ticket_category_id", "config.options")
                    let getSelectedValue = catOptions.filter((x) => { return x.value == y.value })
                    ths.isForServiceRequest = getSelectedValue[0].first_value == "1" ? true : false;
                    if (ths.isForServiceRequest) {
                        ths.FormSchema.forEach(function (g, gi) {
                            if (g.group_name == "Address") {
                                g.visibility = true;
                            }
                        })
                        ths.$refs.AddTicketForm.updateProperty("ticket_for", "value", "2");
                        ths.$refs.AddTicketForm.updateProperty("on_behalf", "value", "4");
                        ths.$refs.AddTicketForm.updateProperty("is_field_service", "value", "1");
                    } else {
                        ths.FormSchema.forEach(function (g, gi) {
                            if (g.group_name == "Address") {
                                g.visibility = false;
                            }
                        })
                        ths.$refs.AddTicketForm.updateProperty("ticket_for", "value", "1");
                        ths.$refs.AddTicketForm.updateProperty("on_behalf", "value", "");
                        ths.$refs.AddTicketForm.updateProperty("is_field_service", "value", "0");
                    }
                }
                if (y.name == "on_behalf") {
                    let type = (y.value == "3") ? "USERS" : "CLIENTS";
                    var url = `id=&moduleName=TICKETING&type=${type}&search=&refId=`;
                    // DataServices.GetDDLData(url).then(function (response) {
                    //     if (response.data) {
                    //         var json = response.data;
                    //         if (json != null) {
                    var labelKey = (y.value == "3") ? "Select User" : "Select Client";
                    ths.$refs.AddTicketForm.updateProperty("on_behalf_consumer_id", "value", "");
                    if (type == "USERS") { ths.$refs.AddTicketForm.updateProperty("on_behalf_consumer_id", "config.options", ths.UserListDLL); }
                    if (type == "CLIENTS") { ths.$refs.AddTicketForm.updateProperty("on_behalf_consumer_id", "config.options", ths.ClientListDLL); }
                    ths.$refs.AddTicketForm.updateProperty("on_behalf_consumer_id", "label", ths.$t(((y.value == "3") ? "User" : "Client")));
                    ths.$refs.AddTicketForm.updateProperty("on_behalf_consumer_id", "placeholder", labelKey);
                    if (type == "USERS") {
                        ths.$refs.AddTicketForm.updateProperty("on_behalf_consumer_id", "config.showAddIcon", false);
                    }
                    if (type == "CLIENTS") {
                        ths.$refs.AddTicketForm.updateProperty("on_behalf_consumer_id", "config.showAddIcon", true);
                    }
                    // ths.$refs.AddTicketForm.schema[0].Data[16].placeholder = labelKey;
                    ths.$refs.AddTicketForm.UpdateKeyValue();
                    //         }
                    //     }
                    // });
                } else if (y.name == "on_behalf_consumer_id") {
                    ths.GetClientaddress(null, z.value);
                    ths.GetCountryDDL();
                } else if (y.name == "START_DATE") {
                    ths.FormSchema.forEach((group, gi) => {
                        group.Data.forEach((field, fi) => {
                            if (field.name == "END_DATE") {
                                field.value = "";
                                field.config.minDate = x;
                            }
                        });
                    });
                }
                else if (y.name == "SA_Scheduled_StartTime") {
                    ths.FormSchema.forEach((group, gi) => {
                        group.Data.forEach((field, fi) => {
                            if (field.name == "SA_Scheduled_EndTime") {
                                field.config.minDate = x;
                            }
                        });
                    });
                }
            }
        },
        GetClientaddress: async function (event, id) {
            var vm = this;
            var clientId = 0;
            if (event == null) {
                clientId = id;
            } else {
                clientId = event.target.value.toString();
                vm.clientId = clientId;
            }
            vm.StaticObj[0].Data[0].config.options = [];
            var appendingUrl = `id=&&moduleName=TICKETING&type=${'CLIENT_ADDRESS_LIST_WO'}&search=&refId=` + clientId;
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        if (response.data.result.DATA != undefined) {
                            response.data.result.DATA.forEach((item, index) => {
                                vm.StaticObj[0].Data[0].config.options.push({
                                    value: item.value,
                                    name: item.name
                                })
                                if (item.first_value == "1") {
                                    vm.StaticObj[0].Data[0].value = item.value;
                                    vm.BindAddressUsingClientAdrress(clientId, item.value);
                                }
                            })
                            vm.$refs.AddTicketForm.UpdateKeyValue();
                        }
                        else {
                            vm.StaticObj[1].Data[0].value = '';
                            vm.StaticObj[1].Data[1].value = '';
                            vm.StaticObj[1].Data[2].value = '';
                            vm.StaticObj[1].Data[3].value = '';
                            vm.StaticObj[1].Data[4].value = '';
                            vm.StaticObj[1].Data[5].value = '';
                            vm.StaticObj[1].Data[6].value = '';
                        }
                    }
                }
            });
            //vm.GetUtilityDDL(event, id);
        },
        GetBase64String: function (evt) {
            try {
                var vm = this;
                var f = evt[0]; // FileList object
                // var f = evt.target.files[0]; // FileList object
                var reader = new FileReader();
                let fileObj = {};
                // Closure to capture the file information.
                reader.onload = (function (theFile) {
                    return function (e) {
                        var binaryData = e.target.result;
                        //Converting Binary Data to base 64
                        var base64StringEncoded = window.btoa(binaryData);
                        //showing file converted to base64
                        var fileName = f.name;
                        vm.fileName = fileName;
                        var n = fileName.lastIndexOf(".");
                        var extensionName = fileName.substring(n + 1, fileName.length);
                        vm.fileExt += "." + extensionName;
                        var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                        var fileSize = f.size;
                        vm.fileSize = fileSize;

                        fileObj.attachement_mime = attachemnetMime,
                            fileObj.fileName = fileName,
                            fileObj.base64String = base64StringEncoded,
                            fileObj.fileSize = f.size,
                            fileObj.contentType = f.type,
                            fileObj.ModuleName = 'TICKETING',
                            fileObj.SubModuleCode = 'TICKETING'
                        vm.fileMulitpleData.push(fileObj);
                    };
                })(f);
                // Read in the image file as a data URL.
                reader.readAsBinaryString(f);

            }
            catch (ex) {
            }
        },
        Close() {
            this.$router.push({ name: "TicketingList" });
        },
        GetDropDownData: function (type, field, id) {
            var vm = this;
            ////debugger;

            var url = `id=&moduleName=TICKETING&type=${type}&search=&refId=`;

            DataServices.GetDDLData(url).then(response => {
                debugger;
                field.config.options = [];
                field.value = '';
                if (response.status == 200) {
                    if (type == "TICKET_CATEGORY") {
                        if (!vm.isBlank(response.data.DATA)) {
                            response.data.DATA.forEach((item) => {
                                field.config.options.push({
                                    value: item.value,
                                    name: item.name
                                })
                            })
                        }
                    }
                    if (type == "CLIENTS") {
                        if (!vm.isBlank(response.data.DATA)) {
                            response.data.DATA.forEach((item) => {
                                field.config.options.push({
                                    value: item.value,
                                    name: item.name
                                })
                            })
                        }
                    }
                }
                vm.$refs.AddTicketForm.UpdateKeyValue();
            })
        },
        getForm: function (id) {
            var ths = this;
            var addressResult = '';
            var url = `recordId=${id}&moduleName=Ticketing&subModuleCode=Ticketing`;
            DataServices.GetDynamicForm(url).then(function (response) {
                if (response != null && response.data != null) {
                    var responseData = response.data.data;
                    var CC_user = response.data.CC_User != "" ? response.data.CC_User[0].cc_user.toString() : ' ';

                    if (response.data.Attachment.length > 0) {
                        var attachmentData = {
                            attachmentName: response.data.Attachment[0].attachment_name,
                            attachmentPath: response.data.Attachment[0].attachment_path
                        };
                    }

                    if (response.data.configData.length > 0) {
                        var configResult = response.data.configData;
                        configResult.forEach(function (row) {
                            if (row.CORE_CONFIG_KEY == "TicketDisallowedFileTypes") {
                                ths.AllowedFileType = row.CONFIG_DATA_VALUE;

                            }

                        });
                    }
                    if (response.data.addressData.length > 0) {
                        addressResult = response.data.addressData;

                    }
                    var frmSchma = []
                    frmSchma = ths.$refs.AddTicketForm.ConvertCustomFieldIntoDynamicFormObj(responseData);
                    var TicketCategoryMandatory, TicketProductMandatory;
                    $.each(configResult, $.proxy(function (index1, item) {
                        if (item.CORE_CONFIG_KEY == "TicketCategoryOptionMandatory") {
                            TicketCategoryMandatory = item.CONFIG_DATA_VALUE;
                        }
                        if (item.CORE_CONFIG_KEY == "TicketProductOptionMandatory") {
                            TicketProductMandatory = item.CONFIG_DATA_VALUE;
                        }
                    }, this))
                    frmSchma.forEach((group, gi) => {
                        group.Data.forEach((field, fi) => {
                            // if (field.astype == 'RadioField') {
                            //         var xvalue = '';
                            //         field.config.options.forEach((x, i) => {
                            //             if (i == 0) {
                            //                 xvalue = x.value
                            //             }
                            //         })
                            //         field.value = xvalue;
                            //     }
                            if (field.name == "is_read") {
                                field.value = 0;
                            }
                            if (field.name == "status_id") {
                                if (field.config.options.length > 0) {
                                    field.value = 1111;
                                }
                            }
                            else if (field.name == "ticket_for" && ths.$route.params.id == "0") {
                                field.value = "1";
                            }
                            else if (field.name == "on_dehalf" && ths.$route.params.id == "0") {
                                field.value = "4";
                            }
                            else if (field.name == "ticket_category_id") {
                                if (TicketCategoryMandatory == "yes") {
                                    field.validationRules.required = true;
                                } else {
                                    field.validationRules.required = false;
                                }
                                field.config.showAddIcon = true;
                                field.config.onAddButtonClick = ths.OpenCategoryModal;
                            }
                            else if (field.name == "assigned_to") {
                                field.visibility = false;
                                field.label = 'Assign To';
                            }
                            else if (field.name == "cc_users") {
                                field.astype = "MultiSelectField";
                                field.mode = "tag";
                                field.value = CC_user;
                            }
                            else if (field.name == "product_id") {
                                if (TicketProductMandatory == "yes") {
                                    field.validationRules.required = true;
                                } else {
                                    field.validationRules.required = false;
                                }
                            }
                            else if (field.name == "is_field_service" && field.value == "1") {
                                ths.isForServiceRequest = true;

                                ths.BindAddressInEdit(addressResult);
                            }
                            else if (field.name == "on_behalf_consumer_id") {
                                ths.$refs.AddTicketForm.updateProperty("on_behalf_consumer_id", "value", field.value);
                                ths.$refs.AddTicketForm.UpdateKeyValue();
                                field.config.showAddIcon = true;
                                field.config.onAddButtonClick = ths.OpenClientModal;
                            }
                            else if (group.group_name == "Preferred Time") {
                                if (field.name == "START_DATE" || field.name == "END_DATE") {
                                    field.config.format = "MM/DD/YYYY h:mm A";
                                    field.config.minDate = "";
                                }
                            }

                            else if (field.name == "attachment") {
                                field.showLabel = false;
                                let fileTypeList = ths.AttachmentDisallowedFileType(ths.AllowedFileType);
                                if (fileTypeList != null) {
                                    field.config =
                                    {
                                        maxSize: 5,
                                        reject: fileTypeList,
                                        showSelectedFiles: [],
                                        onDeleteSelectedFiles: ths.onDeleteSelectedFiles,
                                        //onChange: ths.GetBase64String,
                                        showAddIcon: false,
                                        onAddButtonClick: '',
                                        multiple: true
                                    };

                                    if (attachmentData != null) {
                                        field.config.showSelectedFiles.push(attachmentData);
                                    }
                                }

                            }
                            field.config.onChange = ths.onChangeEventForFields;
                        })
                    })
                    frmSchma.push(
                        {
                            layoutType: "single",
                            group_name: 'ticketType',
                            visibility: false,
                            Data: [{
                                astype: "SlotField",
                                name: "TicketType",
                                value: "",
                                visibility: false,
                            }]
                        });
                    frmSchma.push({
                        layoutType: "single",
                        group_name: 'Address',
                        visibility: false,
                        Data: [{
                            astype: "SlotField",
                            name: "addresFields",
                            value: "",
                            visibility: true,
                            showLabel: false
                        }]
                    });
                    ths.FormSchema = frmSchma;
                }
            });
        },
        OpenCategoryModal: function () {
            var vm = this;
            vm.isManageCategory = true;
        },
        OpenClientModal: function () {
            //debugger
            var vm = this;
            vm.isManageClient = true;
        },
        onDeleteSelectedFiles: function (item) {
            const attachmentConfig = this.findAttachmentConfig();
            if (attachmentConfig) {
                const index = attachmentConfig.showSelectedFiles.indexOf(item);
                if (index > -1) {
                    attachmentConfig.showSelectedFiles.splice(index, 1);
                }
            }
        },
        findAttachmentConfig() {
            for (let item of this.FormSchema) {
                if (item.Data) {
                    for (let dataItem of item.Data) {
                        if (dataItem.label && dataItem.label.toLowerCase().includes('attachment')) {
                            return dataItem.config;
                        }
                    }
                }
            }
            return null;
        },
        saveForm: function (btnType) {
            var ths = this;
            ths.$refs.AddTicket.validate().then(result => {
                if (result) {
                    if (btnType.value == "DRAFT") {
                        ths.FormSchema.forEach((group, gi) => {
                            group.Data.forEach((field, fi) => {
                                if (group.group_name == "ticketType") {
                                    if (field.name == "TicketType") {
                                        field.value = 'DRAFT';
                                    }
                                }
                            });
                        });
                        ths.ticketType = 'DRAFT';
                    }
                    ths.isLoading = true;
                    var CustomFieldArray = ths.$refs.AddTicketForm.ConvertDynamicFormObjToCustomFieldObj(ths.FormSchema);
                    var CustomFieldArrayStatic = ths.$refs.AddTicketForm.ConvertDynamicFormObjToCustomFieldObj(ths.StaticObj);
                    CustomFieldArray.forEach((x) => {
                        if (x.field_name == "on_behalf_consumer_id") {
                            if (!ths.isBlank(x.field_value)) {
                                x.field_value = x.field_value.value;
                            }
                        }
                        if (x.field_name == "cc_users") {
                            if (x.field_value.length > 0) {
                                var CCUsers = '';
                                x.field_value.forEach((row) => {
                                    CCUsers += row.value + ', ';
                                });
                                CCUsers = CCUsers.slice(0, -2);
                                x.field_value = CCUsers;
                            }
                        }
                        if (x.field_name == "weekly") {
                            if (!ths.isBlank(x.field_value)) {
                                x.field_value = x.field_value.value;
                            }
                        }
                        if (x.field_name == "SA_Scheduled_StartTime") {
                            if (x.field_value == "") {
                                x.field_value = null;
                            } else {
                                x.field_value = x.field_value;
                            }
                        }
                        if (x.field_name == "SA_Scheduled_EndTime") {
                            if (x.field_value == "") {
                                x.field_value = null;
                            } else {
                                x.field_value = x.field_value;
                            }
                        }
                    })
                    CustomFieldArrayStatic.forEach((x) => {
                        CustomFieldArray.push(x);
                    });

                    var obj = {
                        data: CustomFieldArray,
                        moduleCode: "Ticketing",
                        subModuleCode: "Ticketing",
                        attachmentData: ths.fileMulitpleData.length > 0 ? ths.fileMulitpleData : null
                    }
                    DataServices.SaveDynamicForm(JSON.stringify(obj)).then(function (response) {
                        ////debugger;
                        ths.isLoading = false;
                        if (response != null && response.data != null && response.data[0].Status == "Success") {
                            ths.TicketNumber = JSON.parse(response.data[0].Value.OTHER_DATA).TICKET_NUMBER;
                            ths.ShowAlert(ths.$t("AddedSuccess", [ths.$t("Ticket")]), "success", true, ths.$t("Alert"));
                            ths.isSuccessMessage = true;
                            if (ths.ticketType == 'DRAFT') {
                                ths.saveTicketStatus = ths.$t("TicketDraftSubmitStatus");
                            }
                            else {
                                ths.saveTicketStatus = ths.$t("TicketSubmitStatus");
                            }

                            ths.showTourGuide = false;
                        } else {
                            ths.ShowAlert(ths.$t("ErrorWhileSaving", [ths.$t("Ticket")]), "danger", true, ths.$t("Alert"));
                            ths.isSuccessMessage = false;
                        }
                    })
                }
            })
        },
        updateCkeditorModel: function (e, x) {
            x.fieldInfo.value = e;
        }
    }
}
</script>